import React, {Component} from "react";
import PropTypes from "prop-types";
import {loadModule, stopModules} from "eshop/modules/core/utils/ui";

export default class MulticartMnpTooltip extends Component {
    constructor (props) {
        super(props);
        this.loaded = false;
    }

    load() {
        if (this.refs.tooltip) {
            loadModule(this.refs.tooltip, {
                path: "core/modules/tooltips",
                options: {
                    triggerSelector: '.o-tooltip__trigger',
                    contentSelector: '.o-tooltip__content',
                    triggerEvent: 'mouseover',
                    maxWidth: 500
                }
            });
            this.loaded = true;
        }
    }

    componentDidMount() {
        this.load();
    }

    componentDidUpdate() {
        if (!this.loaded) {
            this.load();
        } else {
            if (this.refs.tooltip) {
                OPL.UI.initModules( this.refs.mainElement )
            } else {
                this.loaded = false;
            }
        }
    }

    componentWillUnmount() {
        OPL.UI.stopModules( this.refs.mainElement )
    }

    render() {
        return (
            <div ref="mainElement" className={this.props.className}>
            { this.props.content &&
                <span ref="tooltip">
                    <span data-type="no-click" data-tooltip-trigger-event="mouseover" className={this.props.tooltipClassName}></span>
                    <span className="o-tooltip__content">
                        {this.props.content}
                    </span>
                </span>
            }
            </div>
        )
    }
}
MulticartMnpTooltip.defaultProps = {
    tooltipClassName: "o-tooltip__trigger o-tooltip--top g-icon g-icon--only g-icon--hint g-icon--xs-s u-spacing-left-s",
    content: "def: No tooltip content defined"
}
MulticartMnpTooltip.propTypes = {
    tooltipClassName: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired
}